import React, { useState } from 'react';
import { View, Text, Button, StyleSheet, TextInput } from 'react-native';
import Checkbox from 'expo-checkbox';
// import { CheckBox } from 'react-native-check-box';

const TodoItem = ({ item, onDelete, changeText }) => {
    const [isChecked, setChecked] = useState(item.completed);
    const [isRedacting, setRedacting] = useState(false);
    const [text, setText] = useState('');

    const changeStatus = () => {
        item.completed = !item.completed;
        setChecked(item.completed);
    };

    const startRedacting = () => {
        setRedacting(true);
    } 

    const finishRedacting = () => {
        setRedacting(false);
        console.log(text);
        changeText(item, text);
    } 

    if (isRedacting){
        return (
            <View style={styles.todoItem}>
                <Checkbox value={isChecked} onValueChange={() => changeStatus()} />
                {/* input field here */}
                <TextInput                 
                    placeholder='Введите задачу'
                    value={text}
                    onChangeText={setText}
                />
                <Button title='Сохранить' onPress={finishRedacting}/>
                <Button title='Удалить' onPress={() => onDelete(item.id)}/>
            </View>
        );
    } else {
        return (
            <View style={styles.todoItem}>
                <Checkbox value={isChecked} onChange={() => console.log(isChecked)} onValueChange={() => changeStatus()} />
                <Text style={styles.todoText}>{item.text}</Text>
                <Button title='Изменить' onPress={startRedacting}/>
                <Button title='Удалить' onPress={() => onDelete(item.id)}/>
            </View>
        );
    }   
};

const styles = StyleSheet.create({
    todoItem: {
        padding: 10,
        marginVertical: 5,
        backgroundColor: '#f8f9fa',
        borderRadius: 5,
        flexDirection: 'row',
        justifyContent: 'space-between',
        alignItems: 'center',
    },
    todoText: {
        fontSize: 16,
    },
});

export default TodoItem;