import React, { useState, useEffect } from 'react';
import { View, TextInput, Button, StyleSheet } from 'react-native';
import TodoList from '../components/TodoList';
import AsyncStorage from '@react-native-async-storage/async-storage';

const HomeScreen = () => {

    const [savedValue, setSavedValue] = useState('');

	useEffect(() => {
		const loadData = async () => {
			try {
				const value = await AsyncStorage.getItem("myKey");
				if (value !== null) {
					setSavedValue(value);
				}
			} catch (e) {
				console.log("Ошибка при загрузке данных", e);
			}
		};
		loadData();
	}, []);

	const saveData = async () => {
		try {
			await AsyncStorage.setItem("myKey", todos);
            AsyncStorage.setItem(todos);
		} catch (e) {
            console.log("Ошибка при получении данных", e);
        }
	};


    const [text, setText] = useState('');
    const [todos, setTodos] = useState([
        { id: 1, text: "New Tab", completed: false }
    ]);

    const addTodo = () => {
        if (text.trim()) {
            setTodos([...todos, { id: Date.now(), text, completed: false }]);
            setText('');
        }
    };

    const changeText = (todo, text) => {
        todo.text = text;
        setTodos([...todos]);
    }

    const deleteTodo = (id) => {
        setTodos(todos.filter(todo => todo.id !== id));
    };

    return (
        <View style={styles.container}>
            <TextInput
            style={styles.input}
            placeholder='Введите задачу'
            value={text}
            onChangeText={setText}
            />
            <Button title='Добавить' onPress={addTodo}/>
            <TodoList todos={todos} onDelete={deleteTodo} changeText={changeText}/>
            <Button title='Сохранить' onPress={saveData}/>
        </View>
    )
};

const styles = StyleSheet.create({
    container: {
        flex: 1,
        padding: 20,
    },
    input: {
        padding: 10,
        borderColor: '#ccc',
        borderWidth: 1,
        borderRadius: 5,
        marginBottom: 10,
    },
});

export default HomeScreen;